.modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 1000;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0);
  backdrop-filter: blur(4px);
  transition: background-color 0.5s, width 0.1s 0.5s, height 0.1s 0.5s, padding 0.1s 0.5s;
}

.modal__dialog {
  z-index: 1001;
  width: 600px;
  max-width: 100%;
  transform: translate3d(0, -100%, 0);
  opacity: 0;
  visibility: hidden;
}

/**
 * Active State
 */
.modal.is-active {
  width: 100%;
  height: 100%;
  padding: 1em;
  background-color: rgba(0, 0, 0, 0.75);
  transition: background-color 0.5s;
}

.modal.is-active .modal__dialog {
  transform: translate3d(0, 0, 0);
  opacity: 1;
  visibility: visible;
}

.modal_full .modal__dialog {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  border: none;
  border-radius: 0;
  transform: translate3d(0, -25%, 0);
}

.modal_pos_top {
  justify-content: flex-start;
}

.modal_pos_bottom {
  justify-content: flex-end;
}

.modal_pos_bottom .modal__dialog {
  transform: translate3d(0, 100%, 0);
}

.modal_pos_left .modal__dialog,
.modal_pos_right .modal__dialog {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 280px;
  height: 100%;
  margin: 0;
  border-radius: 0;
  opacity: 1;
  border: none;
}

.modal_pos_left .modal__dialog {
  left: 0;
  transform: translate3d(-100%, 0, 0);
}

.modal_pos_right .modal__dialog {
  right: 0;
  transform: translate3d(100%, 0, 0);
}

.modal_size_small .modal__dialog {
  width: 300px;
}

.modal_size_large .modal__dialog {
  width: 900px;
}

