.tooltip {
  position: relative;
}

.tooltip::after {
  content: attr(data-tooltip);
  pointer-events: none;
  position: absolute;
  z-index: 100;
  top: auto;
  left: 50%;
  right: auto;
  bottom: 100%;
  display: block;
  max-width: 30em;
  margin: 0;
  padding: 0.3125em 0.75em;
  font-size: 0.85rem;
  color: #ffffff;
  background: rgba(33, 33, 33, 0.95);
  border-radius: 3px;
  opacity: 0;
  transition: opacity 0.25s, transform 0.25s;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  transform: translate(-50%, 0);
}

.tooltip:hover::after, .tooltip:focus::after {
  opacity: 1;
  transform: translate(-50%, -0.5em);
}

/**
 * Up
 */
.tooltip_pos_up::after {
  top: auto;
  left: 50%;
  right: auto;
  bottom: 100%;
  transform: translate(-50%, 0);
}

.tooltip_pos_up:hover::after, .tooltip_pos_up:focus::after {
  opacity: 1;
  transform: translate(-50%, -0.5em);
}

.tooltip_pos_up-left::after {
  top: auto;
  left: 0;
  right: auto;
  bottom: 100%;
  transform: translate(0, 0);
}

.tooltip_pos_up-left:hover::after, .tooltip_pos_up-left:focus::after {
  opacity: 1;
  transform: translate(0, -0.5em);
}

.tooltip_pos_up-right::after {
  top: auto;
  left: auto;
  right: 0;
  bottom: 100%;
  transform: translate(0, 0);
}

.tooltip_pos_up-right:hover::after, .tooltip_pos_up-right:focus::after {
  opacity: 1;
  transform: translate(0, -0.5em);
}

/**
 * Left
 */
.tooltip_pos_left::after {
  top: 50%;
  left: auto;
  right: 100%;
  bottom: auto;
  transform: translate(0, -50%);
}

.tooltip_pos_left:hover::after, .tooltip_pos_left:focus::after {
  transform: translate(-0.5em, -50%);
}

.tooltip_pos_left-up::after {
  top: 0;
  left: auto;
  right: 100%;
  bottom: auto;
  transform: translate(0, 0);
}

.tooltip_pos_left-up:hover::after, .tooltip_pos_left-up:focus::after {
  transform: translate(-0.5em, 0);
}

.tooltip_pos_left-down::after {
  top: auto;
  left: auto;
  right: 100%;
  bottom: 0;
  transform: translate(0, 0);
}

.tooltip_pos_left-down:hover::after, .tooltip_pos_left-down:focus::after {
  transform: translate(-0.5em, 0);
}

/**
 * Right
 */
.tooltip_pos_right::after {
  top: auto;
  left: 100%;
  right: auto;
  bottom: 50%;
  transform: translate(0, 50%);
}

.tooltip_pos_right:hover::after, .tooltip_pos_right:focus::after {
  transform: translate(0.5em, 50%);
}

.tooltip_pos_right-up::after {
  top: 0;
  left: 100%;
  right: auto;
  bottom: auto;
  transform: translate(0, 0);
}

.tooltip_pos_right-up:hover::after, .tooltip_pos_right-up:focus::after {
  transform: translate(0.5em, 0);
}

.tooltip_pos_right-down::after {
  top: auto;
  left: 100%;
  right: auto;
  bottom: 0;
  transform: translate(0, 0);
}

.tooltip_pos_right-down:hover::after, .tooltip_pos_right-down:focus::after {
  transform: translate(0.5em, 0);
}

/**
 * Down
 */
.tooltip_pos_down::after {
  top: 100%;
  left: auto;
  right: 50%;
  bottom: auto;
  transform: translate(50%, 0);
}

.tooltip_pos_down:hover::after, .tooltip_pos_down:focus::after {
  opacity: 1;
  transform: translate(50%, 0.5em);
}

.tooltip_pos_down-left::after {
  top: 100%;
  left: 0;
  right: auto;
  bottom: auto;
  transform: translate(0, 0);
}

.tooltip_pos_down-left:hover::after, .tooltip_pos_down-left:focus::after {
  opacity: 1;
  transform: translate(0, 0.5em);
}

.tooltip_pos_down-right::after {
  top: 100%;
  left: auto;
  right: 0;
  bottom: auto;
  transform: translate(0, 0);
}

.tooltip_pos_down-right:hover::after, .tooltip_pos_down-right:focus::after {
  opacity: 1;
  transform: translate(0, 0.5em);
}

/**
 * Cover
 */
.cover {
  display: flex;
  align-items: stretch;
  flex-direction: column;
}

@media (min-width: 760px) {
  .cover {
    flex-direction: row;
    height: 100vh;
    overflow: hidden;
  }
}

/**
 * Header and Content
 */
.cover__header,
.cover__content {
  -webkit-overflow-scrolling: touch;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
}

.cover__content {
  flex: 1 1 auto;
}

.cover__header {
  flex: 0 0 auto;
  display: flex;
  color: #ffffff;
  background: #0a6ebd;
}

@media (min-width: 760px) {
  .cover__header,
  .cover__content {
    flex: 1 0 0;
  }
  .cover__header {
    flex-grow: 2;
    flex-direction: column;
    justify-content: center;
  }
}

/**
 * Inner
 */
.cover__inner {
  padding: 1.5em;
}

@media (min-width: 480px) {
  .cover__inner {
    padding: 2em;
  }
}

@media (min-width: 760px) {
  .cover__inner {
    max-width: 42em;
    padding: 4em;
  }
}

/**
 * Title, description and version
 */
.cover__title {
  font-family: Constantia, "Lucida Bright", Lucidabright, "Lucida Serif", Lucida, "DejaVu Serif", "Bitstream Vera Serif", "Liberation Serif", Georgia, serif;
  font-size: 2.5em;
  line-height: 1.325em;
  font-weight: normal;
  margin-bottom: 1rem;
}

.cover__desc {
  line-height: 1.6em;
  margin-bottom: 1rem;
}

.cover__version {
  color: rgba(255, 255, 255, 0.25);
}

@media (min-width: 760px) {
  .cover__title {
    font-size: 5em;
  }
  .cover__desc {
    font-size: 1.4em;
  }
}

.jumbo-filter {
  position: relative;
  padding: 0.5em;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.jumbo-filter > * + * {
  margin-top: 0.25em;
}

@media (min-width: 480px) {
  .jumbo-filter {
    padding: 1em;
  }
  .jumbo-filter > * + * {
    margin-top: 0.5em;
  }
}

.jumbo-list {
  position: relative;
  list-style: none;
}

.jumbo-list__item {
  position: relative;
}

/**
 * Menu Link
 */
.jumbo-list__link {
  display: block;
  padding: 1.25em 1.5em;
  line-height: 1.325em;
  background-color: #ffffff;
  transition: background-color 0.5s linear;
}

.jumbo-list__link:hover, .jumbo-list__link:focus {
  outline: none;
  background-color: #eceff1;
  transition: background-color 0s linear;
}

@media (min-width: 480px) {
  .jumbo-list__link {
    padding: 1.5em 2em;
  }
}

/**
 * Page
 */
.page {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}

/* Header & Footer */
.page__header,
.page__footer {
  flex: 0 0 auto;
  padding: 0.5em;
}

.page__header {
  background: #0a6ebd;
  color: #ffffff;
}

.page__footer {
  background: #eceff1;
}

@media (max-width: 619px) {
  .page__footer .grid_sm > .grid__item + .grid__item {
    margin-top: -1.5em;
  }
}

@media (min-width: 760px) {
  .page__header,
  .page__footer {
    padding: 1em;
  }
  .page__footer .grid_md > .grid__item + .grid__item {
    margin-top: -1.5em;
  }
}

/* Main */
.page__main {
  flex: 1 1 auto;
  overflow-x: hidden;
  overflow-y: auto;
}

/* Section */
.page__section {
  padding: 1.5em;
}

@media (min-width: 760px) {
  .page__section {
    padding: 2em;
  }
}

